import React from 'react';
import Draggable from 'react-draggable';
import { useDispatch } from 'react-redux';
import { setIsIntersect } from './reducers/intersect';

const Rects = () => {
    const dispatch = useDispatch();
    // 선택한 상자가 무조건 위에 뜨게 하는 함수
    const onTop = (e) => {
        const rects = document.querySelectorAll('.rect');
        rects.forEach((el) => (el.style.zIndex = 0));
        e.target.style.zIndex = 10;
    };
    // 상자끼리 교차하는지 확인하는 함수
    const isIntersectedFn = (e) => {
        // 드래그가 가능한 상자를 Array에 담기
        const reactDraggable = document.querySelectorAll('.react-draggable');
        let rectsArr = [];
        reactDraggable.forEach((el) => rectsArr.push(el));
        // 드래그하는 중인 상자
        const dragItem = e.target;
        const dragBBox = e.target.getBoundingClientRect();
        // 드래그하는 상자와는 다른 상자
        const anotherRect = rectsArr.find((el) => el.id !== e.target.id);
        const anotherRectBBox = anotherRect.getBoundingClientRect();
        // 상자끼리 교차하는지 여부
        if (
            dragItem.classList.contains('react-draggable') &&
            dragBBox.x + dragBBox.width >= anotherRectBBox.x &&
            dragBBox.x <= anotherRectBBox.x + anotherRectBBox.width &&
            dragBBox.y <= anotherRectBBox.y + anotherRectBBox.height &&
            dragBBox.y + dragBBox.height >= anotherRectBBox.y
        ) {
            dispatch(setIsIntersect(true));
        } else {
            dispatch(setIsIntersect(false));
        }
    };
    return (
        <div className='drag-area'>
            <Draggable grid={[1, 1]} onMouseDown={(e) => onTop(e)} onStop={(e) => isIntersectedFn(e)} bounds='parent'>
                <div id='rect1' className='rect rect1'></div>
            </Draggable>
            <Draggable grid={[1, 1]} onMouseDown={(e) => onTop(e)} onStop={(e) => isIntersectedFn(e)} bounds='parent'>
                <div id='rect2' className='rect rect2'></div>
            </Draggable>
        </div>
    );
};

export default Rects;
