// 액션
const INTERSECT = 'intersect/INTERSECT';

// 액션 생성 함수
export const setIsIntersect = (isIntersected) => ({ type: INTERSECT, isIntersected });

// 초기값
const initialState = {
    is: false
};

// 리덕스 스토어값 변경
export default function intersect(state = initialState, action) {
    switch (action.type) {
        case INTERSECT:
            return { is: action.isIntersected };
        default:
            return state;
    }
}
